                <v-btn
                    color="warning"
                    @click="onRepairs"
                >
                    设备报修
                </v-btn>
            </v-col>
            <v-col cols="4" class="text-center">
                <v-btn
                    color="#00E5FF"
                    @click="onReceiveFunc"
